import { DifficultyPage } from './components/difficulty/difficulty-component';
import { Game } from './components/game/game';
import "./styles/index.css";
var app = document.getElementById('root');
var page = 'Difficulty';
export var goToPage = function (newPage) {
    page = newPage;
    renderApp();
};
var renderApp = function () {
    switch (page) {
        case 'Difficulty':
            DifficultyPage(app);
            break;
        case 'Game':
            Game(app);
            break;
        default:
            console.log('404');
            break;
    }
};
// start
renderApp();
